import { Box } from '@material-ui/core';
import {
  Link,
  ResponseErrorPanel,
  Table,

} from '@backstage/core-components';

import React from 'react';

import { AzureGitTagsIcon } from '../AzureGitTagsIcon';
import { useEntity } from '@backstage/plugin-catalog-react';
import { useGitTags } from '../../hooks/useGitTags';

const columns = [
  {
    title: 'Tag',
    field: 'name',
    highlight: false,
    defaultSort: 'desc',
    width: 'auto',
    render: (row) => (
      React.createElement(Box, { display: "flex", alignItems: "center",}
        , React.createElement(Link, { to: row.link ?? '',}, row.name)
      )
    ),
  },
  {
    title: 'Commit',
    field: 'peeledObjectId',
    width: 'auto',
    render: (row) => (
      React.createElement(Box, { display: "flex", alignItems: "center",}
        , React.createElement(Link, { to: row.commitLink ?? '',}, row.peeledObjectId)
      )
    ),
  },
  {
    title: 'Created By',
    field: 'createdBy',
    width: 'auto',
  },
];

export const GitTagTable = () => {
  const { entity } = useEntity();

  const { items, loading, error } = useGitTags(entity);

  if (error) {
    return React.createElement(ResponseErrorPanel, { error: error,} );
  }

  return (
    React.createElement(Table, {
      isLoading: loading,
      columns: columns,
      options: {
        search: true,
        paging: true,
        pageSize: 5,
        showEmptyDataSourceMessage: !loading,
      },
      title: 
        React.createElement(Box, { display: "flex", alignItems: "center",}
          , React.createElement(AzureGitTagsIcon, { style: { fontSize: 30 },} )
          , React.createElement(Box, { mr: 1,} ), "Azure Repos - Git Tags ("
               , items ? items.length : 0, ")"
        )
      ,
      data: items ?? [],}
    )
  );
};
